@import '../../../base';

.check-out-popup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;

  p {
    padding: 0 10px;
  }
  img {
    max-width: 90%;
    z-index: -1;

    @media screen and (max-width: 576px) {
      max-width: 60%;
    }
  }

  .bounce-in {
    animation-delay: 3s;
    animation: bounceIn 1s;
  }

  .upper-container {
    p,
    span {
      text-align: center;
      font-size: $font-size-big;
      font-weight: $font-weight-semibold;
      color: $color-title;

      @media screen and (max-width: 576px) {
        font-size: $font-size-regular;
      }
    }

    .focus-text {
      color: $color-hover;
    }
  }

  .upper-margin-up {
    margin-top: -50px;
  }

  .upper-margin-down {
    margin-top: 30px;
  }

  .extra-text,
  .mid-container {
    p,
    span {
      text-align: center;
      font-size: $font-size-small;
      font-weight: $font-weight-light;
      color: $color-caption;
      max-width: 350px;

      @media screen and (max-width: 576px) {
        font-size: $font-size-smallest;
      }
    }
  }

  .button-container {
    display: flex;
    margin-top: 27px;

    button {
      margin: 15px;
    }

    @media screen and (max-width: 576px) {
      margin-top: 10px;
      // flex-direction: column;

      button {
        min-width: 100px !important;
        min-height: 46px !important;
        p {
          font-size: $font-size-small !important;
        }
      }
    }
  }
}